.complete-modal {
	position: absolute;
	top: 67vh;
	width: 37.52vw;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background: #ffffff;
	border: 1px solid rgba(0, 0, 0, 0.0497193);
	border-radius: 8px;
	padding: 48px;
	z-index: 3;
	gap: 3rem;
	@media screen and (max-width: 800px) {
		padding: 32px 24px 40px;
		width: 85vw;
		top: 20vh;
		button {
			width: 107px;
			height: 48px;
		}
	}
	&__content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 17px;
		h3 {
			font-size: 1.5rem;
		}
		p {
			font-size: 1rem;
		}
	}
}
