.hero{display:flex;flex-direction:column;height:400px;width:100%;background-image:url(/crowdfunding-product-page-/static/media/image-hero-desktop.631209c1.jpg);background-repeat:no-repeat;background-position:center center;background-size:cover}@media screen and (max-width: 800px){.hero{height:300px;background-image:url(/crowdfunding-product-page-/static/media/image-hero-mobile.d4f477d4.jpg)}}.hero .header{padding:0 165px;height:128px;display:flex;justify-content:space-between;align-items:center}@media screen and (max-width: 800px){.hero .header{padding:32px 24px;height:0;position:relative;z-index:3}}.hero .header .logo-container{height:100%;width:70px;padding:25px}.hero .header .nav-desktop{width:70%;display:flex;justify-content:flex-end;grid-gap:35px;gap:35px}.hero .header .nav-desktop--item{display:inline-block;font-weight:500;color:#fff}@media screen and (max-width: 800px){.hero .header .nav-desktop{display:none}}.hero .header .nav-mobile{display:none}.hero .header .nav-mobile__icon{cursor:pointer}@media screen and (max-width: 800px){.hero .header .nav-mobile{display:flex}}.hero .nav-mobile__items{width:85vw;display:flex;flex-direction:column;align-self:center;background:#ffffff;border:1px solid rgba(0,0,0,0.0497193);border-radius:8px;z-index:3}.hero .nav-mobile__items--item{padding:24px;font-family:"Roboto";font-weight:500;color:#000;cursor:pointer}.hero .nav-mobile__items--item:nth-of-type(2){border-top:1px solid rgba(0,0,0,0.0497193);border-bottom:1px solid rgba(0,0,0,0.0497193)}.nav-mobile__overlay{position:fixed;top:0;left:0;width:100%;height:100%;background:linear-gradient(180deg, #000 0%, rgba(0,0,0,0.0001) 100%);opacity:0.6;z-index:2}

